.OUT-OF-STOCK {
    background-color: rgb(126, 29, 29, 0.245);
    color: white;
    width: 350px;
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    height: 500px;
}

.LOW-STOCK {
    background-color: rgba(213, 106, 24, 0.245);
    color: white;
    width: 350px;
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    height: 500px;
}
.HIGH-MONEY {
    background-color: rgba(0, 128, 0, 0.245);
    color: white;
    width: 400px;
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    height: 500px;
}


.CARD-OG {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: center;
    align-items: center;
    gap: 20px;
}

.CARD {
    width: 350px;
    justify-content: center;
    align-items: center;
    display: flex;
    background-color: rgba(119, 34, 188, 0.312);
    color: white;
    flex-direction: column;
    padding-bottom: 15px;
    height: 500px;
    padding-top: 15px;
}

.BTN-PIXEL {
    width: 120px;
  }
  
.IMAGES-PRODUCTS {
    width: 200px;
    height: 170px;
}